//vuex
import Vue from 'vue';
import Vuex from 'vuex';
import mutations from './mutations.js';
import actions from './actions.js';
//
Vue.use(Vuex);

//Store
const store = new Vuex.Store({
    //
    state:{
        topics:[
            {name:'all',desc:''},
            {name:'good',desc:''},
            {name:'share',desc:''},
            {name:'ask',desc:''},
            {name:'job',desc:''},
            {name:'dev',desc:''}
          ],
          unLoginedMmoreMenus:{
            login:'',
            
          },
          logindeMoreMenus:{
            create:'',
            center:'',
            msg:'',
            logout:''
          },
          isLogind:false,
          //
          loginname:'',
          //accesstoken
          accesstoken:'',
          //id
          author_id:'',
          //
          isShowMoreMenus:false
    },
    //
    getters:{
        moreMenus: function(state) {
            if (state.isLogind) {
              //
              return state.logindeMoreMenus;
            }
            return state.unLoginedMmoreMenus;
          },
          getTopicDescByTab: function(state){
            //
            return function(tab){
              for(var index=0;index<state.topics.length;index++){
                if(state.topics[index].name == tab){
                  return state.topics[index].desc
                }
              }
            }
          }
    },
    mutations,
    actions
})


//
export default store;


